
/* eslint-disable no-restricted-globals */

self.onmessage = function (event) {
    const { arr, row, colors } = event.data;
  
    const pieChartColors = arr.map((item) => {
      const value = row[item.id];
      return value === 0 ? colors[0] :
             value === 1 ? colors[1] :
             value === 2 ? colors[2] :
             value === 3 ? colors[3] : colors[4];
    });
  
    // Send results back to main thread
    self.postMessage(pieChartColors);
  };
  